const StyleProcessingAnimation: React.FC<StyleProcessingAnimationProps> = ({
  isVisible,
}) => {
  const { t } = useTranslation();

  const timeline = {
    codeDisplay: { start: 0.3, duration: 0.2 },
    cssHighlight: { start: 0.5, duration: 0.3 },
    cssomTree: { start: 0.8, duration: 0.5 },
    selectorMatching: { start: 1.3, duration: 0.3 },
    cascadePriority: { start: 1.6, duration: 0.4 },
    styleResult: { start: 2.0, duration: 0.5 },
  };

  const fadeInVariants = {
    hidden: { opacity: 0 },
    visible: (custom: number) => ({
      opacity: 1,
      transition: { delay: custom, duration: 0.3 },
    }),
  };

  const htmlCode = `
<header>
  <h1>${t('animations.domTree.title')}</h1>
</header>
<main>
  <p>${t('animations.domTree.content')}</p>
</main>`.trim();

  const cssCode = `
header {
  background: #f0f9ff;
  padding: 2rem;
}

h1 {
  color: #1e40af;
  font-size: 2rem;
}

main {
  margin: 2rem;
}

p {
  line-height: 1.6;
}`.trim();

  return (
    <Container>
      <FlexContainer>
        <CodePanel
          variants={fadeInVariants}
          custom={timeline.codeDisplay.start}
          initial="hidden"
          animate={isVisible ? "visible" : "hidden"}
        >
          <DownloadAndParseIndicator
            variants={fadeInVariants}
            custom={timeline.cssomTree.start}
            initial="hidden"
            animate={isVisible ? "visible" : "hidden"}
          >
            <IndicatorStatus>
              <StatusDot />
              <StatusText>{t('animations.styleProcessing.steps.downloadParse')}</StatusText>
            </IndicatorStatus>
          </DownloadAndParseIndicator>

          <CodeBlock2>
            <CodeLabel>CSS</CodeLabel>
            <CodeContent>
              {cssCode}
              <CodeHighlight
                initial={{ opacity: 0, top: "0%", height: "18%" }}
                animate={isVisible ? { opacity: 1 } : { opacity: 0 }}
                transition={{
                  delay: timeline.cssHighlight.start,
                  duration: 0.1,
                }}
              />
              <CodeHighlight
                initial={{ opacity: 0, top: "25%", height: "18.6%" }}
                animate={isVisible ? { opacity: 1 } : { opacity: 0 }}
                transition={{
                  delay: timeline.cssHighlight.start + 0.3,
                  duration: 0.1,
                }}
              />
              <CodeHighlight
                initial={{ opacity: 0, top: "51%", height: "16%" }}
                animate={isVisible ? { opacity: 1 } : { opacity: 0 }}
                transition={{
                  delay: timeline.cssHighlight.start + 0.6,
                  duration: 0.1,
                }}
              />
              <CodeHighlight
                initial={{ opacity: 0, top: "72%", height: "16%" }}
                animate={isVisible ? { opacity: 1 } : { opacity: 0 }}
                transition={{
                  delay: timeline.cssHighlight.start + 0.9,
                  duration: 0.1,
                }}
              />
            </CodeContent>
          </CodeBlock2>
        </CodePanel>

        <VisualizationPanel>
          <StatusIndicator
            variants={fadeInVariants}
            custom={timeline.cssomTree.start}
            initial="hidden"
            animate={isVisible ? "visible" : "hidden"}
          >
            <IndicatorStatus>
              <StatusDot />
              <StatusText>{t('animations.styleProcessing.steps.convertRules')}</StatusText>
            </IndicatorStatus>
          </StatusIndicator>

          <StatusIndicator
            variants={fadeInVariants}
            custom={timeline.styleResult.start}
            initial="hidden"
            animate={isVisible ? "visible" : "hidden"}
            style={{ top: "41%" }}
          >
            <IndicatorStatus>
              <StatusDot />
              <StatusText>{t('animations.styleProcessing.steps.calculateSpecificity')}</StatusText>
            </IndicatorStatus>
          </StatusIndicator>

          <RootNodeCard
            variants={fadeInVariants}
            custom={timeline.cssomTree.start}
            initial="hidden"
            animate={isVisible ? "visible" : "hidden"}
            style={{ marginTop: "3.5%" }}
          >
            <NodeTitle>Stylesheet</NodeTitle>
            <NodeContent>root node</NodeContent>
          </RootNodeCard>

          <NodeCard
            variants={fadeInVariants}
            custom={timeline.cssomTree.start + 0.1}
            initial="hidden"
            animate={isVisible ? "visible" : "hidden"}
          >
            <NodeTitle>header</NodeTitle>
            <NodeContent>background, padding</NodeContent>
          </NodeCard>

          <NodeCard
            variants={fadeInVariants}
            custom={timeline.cssomTree.start + 0.3}
            initial="hidden"
            animate={isVisible ? "visible" : "hidden"}
          >
            <NodeTitle>h1</NodeTitle>
            <NodeContent>color, font-size</NodeContent>
          </NodeCard>

          <NodeCard
            variants={fadeInVariants}
            custom={timeline.cssomTree.start + 0.6}
            initial="hidden"
            animate={isVisible ? "visible" : "hidden"}
          >
            <NodeTitle>main</NodeTitle>
            <NodeContent>margin</NodeContent>
          </NodeCard>

          <NodeCard
            variants={fadeInVariants}
            custom={timeline.cssomTree.start + 0.9}
            initial="hidden"
            animate={isVisible ? "visible" : "hidden"}
          >
            <NodeTitle>p</NodeTitle>
            <NodeContent>line-height</NodeContent>
          </NodeCard>

          <StylePriorityBox
            variants={fadeInVariants}
            custom={timeline.cascadePriority.start}
            initial="hidden"
            animate={isVisible ? "visible" : "hidden"}
          >
            <ConflictExample>
              <ConflictRule>
                <CodeText>
                  p {"{"} color: blue; {"}"}
                </CodeText>
                <div>
                  <span>{t('animations.styleProcessing.specificity.weight')} 0,0,0,1</span>
                </div>
              </ConflictRule>
              <hr />
              <ConflictRule>
                <CodeText>
                  main p {"{"} color: red; {"}"}
                </CodeText>
                <div>
                  <span>{t('animations.styleProcessing.specificity.weight')} 0,0,0,2</span>
                </div>
              </ConflictRule>

              <WeightExplanation>
                <div>{t('animations.styleProcessing.specificity.format')}</div>
                <ExplanationItem>{t('animations.styleProcessing.specificity.comparison')}</ExplanationItem>
                <ExplanationItem>{t('animations.styleProcessing.specificity.priority')}</ExplanationItem>
                <ExplanationItem>{t('animations.styleProcessing.specificity.conflict')}</ExplanationItem>
              </WeightExplanation>
              <StyleResult>
                <div style={{ fontWeight: 600, marginBottom: "0.5rem" }}>
                  {t('animations.styleProcessing.specificity.question')}
                </div>
                <div>
                  {t('animations.styleProcessing.specificity.answer')}{" "}
                  <span style={{ color: "red", fontWeight: 500 }}>{t('animations.styleProcessing.specificity.result')}</span>
                </div>
              </StyleResult>
            </ConflictExample>
          </StylePriorityBox>

          <CodeBlock style={{ marginTop: "15%", height: "auto", minHeight: "15rem" }}>
          <StatusIndicator
            variants={fadeInVariants}
            custom={timeline.styleResult.start}
            initial="hidden"
            animate={isVisible ? "visible" : "hidden"}
            style={{ top: "-8%", right: "-31%" }}
          >
            <IndicatorStatus>
              <StatusDot />
              <StatusText>{t('animations.styleProcessing.steps.buildCSSOM')}</StatusText>
            </IndicatorStatus>
          </StatusIndicator>
            <CodeLabel>CSSOM Tree</CodeLabel>
            <CodeContent>
{`Stylesheet
├── header
│   ├── background: #f0f9ff
│   └── padding: 2rem
├── h1
│   ├── color: #1e40af
│   └── font-size: 2rem
├── main
│   └── margin: 2rem
└── p
    └── line-height: 1.6`}
            </CodeContent>
          </CodeBlock>
        </VisualizationPanel>
      </FlexContainer>
    </Container>
  );
};

export default StyleProcessingAnimation;
